export class HomeView {
  constructor() {
    this.app = document.getElementById("app");
  }

  render(mountains) {
    this.app.innerHTML = `
            <header>
                <nav-bar></nav-bar>
            </header>

            <section class="hero" style="background-image: url('${require("../assets/images/cartenz.jpg")}')" aria-label="Hero section">
                <div class="hero-content">
                    <h1>JELAJAHI GUNUNG DI INDONESIA</h1>
                    <form class="search-container" role="search" aria-label="Pencarian gunung">
                        <input type="search" placeholder="Cari gunung di Indonesia" id="searchInput" aria-label="Cari gunung di Indonesia"/>
                        <button type="submit" class="search-btn" aria-label="Tombol cari">
                            <img src="${require("../assets/icon/search-icon.svg")}" alt="Icon pencarian"/>
                        </button>
                    </form>
                </div>
            </section>

            <main>
                <section class="mulai-jelajah-section" aria-labelledby="jelajah-heading">
                    <h2 id="jelajah-heading">Mulai Jelajahi dari Sini</h2>
                    <div class="card-container">
                        ${this.renderMountainCards(mountains.slice(0, 4))}
                    </div>
                </section>

                <section class="destinasi-populer-section" aria-labelledby="populer-heading">
                    <h2 id="populer-heading">Destinasi Populer</h2>
                    <div class="card-container">
                        ${this.renderMountainCards(mountains.slice(0, 4))}
                    </div>
                </section>

                <section class="feature-section">
                <h2>Perencanaan Cerdas dengan Fitur HiPlan</h2>
                    <div class="feature-container">
                        <div class="feature-image">
                            <img src="${require("../assets/images/lawu.jpg")}" alt="Pendaki di gunung" />
                        </div>
                        <div class="feature-content">
                            <p>Semangat petualangan memanggil jiwa untuk menjelajahi ketinggian, seperti rekan pendaki di samping ini. Namun, di balik setiap pendakian hebat, ada perencanaan yang matang.</p>
                            <p>Seringkali kita bertanya: Kapan cuaca terbaik untuk mendaki? Apakah gunung ini sesuai dengan kemampuan saya? Berapa lama waktu yang dibutuhkan? Mencari jawaban yang akurat bisa jadi tantangan.</p>
                            <p>HiPlan hadir sebagai solusi. Kami mengubah keraguan menjadi kepastian dengan menyediakan prediksi tren cuaca bulanan, analisis tingkat kesulitan gunung, estimasi waktu tempuh, dan rekomendasi yang dipersonalisasi. Semua berbasis data untuk perencanaan yang lebih cerdas.</p>
                        </div>
                    </div>
                </section>
            </main>
        `;

    this.bindEvents();
  }

  renderMountainCards(mountains) {
    return mountains
      .map(
        (mountain) => `
            <article class="card">                <img src="${require("../assets/images/" +
              mountain.image)}" alt="Pemandangan ${mountain.name}" />
                <div class="card-content">
                    <h3>${mountain.name}</h3>
                    <p>${mountain.location}</p>
                    <p class="altitude">${mountain.altitude}</p>
                </div>
            </article>
        `
      )
      .join("");
  }
  bindEvents() {
    const searchForm = document.querySelector(".search-container");
    const searchInput = document.getElementById("searchInput");
    const loginButton = document.querySelector(".btn-masuk");
    const registerButton = document.querySelector(".btn-daftar");
    const hamburgerMenu = document.querySelector(".hamburger-menu");
    const navMenu = document.querySelector(".nav-menu");
    const navbar = document.querySelector(".navbar");

    // Toggle mobile menu
    hamburgerMenu.addEventListener("click", () => {
      hamburgerMenu.classList.toggle("active");
      navMenu.classList.toggle("active");
      navbar.classList.toggle("menu-active");
      hamburgerMenu.setAttribute(
        "aria-expanded",
        hamburgerMenu.classList.contains("active")
      );
    });

    // Close menu when clicking outside
    document.addEventListener("click", (e) => {
      if (!navMenu.contains(e.target) && !hamburgerMenu.contains(e.target)) {
        hamburgerMenu.classList.remove("active");
        navMenu.classList.remove("active");
        navbar.classList.remove("menu-active");
        hamburgerMenu.setAttribute("aria-expanded", "false");
      }
    });

    // Handle search form submission
    searchForm.addEventListener("submit", (e) => {
      e.preventDefault();
      if (this.onSearch) {
        this.onSearch(searchInput.value);
      }
    });

    // Handle login button click
    loginButton.addEventListener("click", () => {
      if (this.onLogin) {
        this.onLogin();
      }
    });

    // Handle register button click
    registerButton.addEventListener("click", () => {
      if (this.onRegister) {
        this.onRegister();
      }
    });
  }

  setLoginHandler(handler) {
    this.onLogin = handler;
  }

  setRegisterHandler(handler) {
    this.onRegister = handler;
  }

  setSearchHandler(handler) {
    this.onSearch = handler;
  }

  updateSearchResults(mountains) {
    // Update the card containers with search results
    const containers = document.querySelectorAll(".card-container");
    containers.forEach((container) => {
      container.innerHTML = this.renderMountainCards(mountains);
    });
  }
}
